.card-item{
    position: relative;
    margin-bottom: 20px;
    overflow: hidden;
    transition: 0.5s;
}
.card-item-info{
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    top: 0;
    background: rgba(0, 0, 0, 0.4);
    z-index: 1;
    transition: all 0.3s;
}
.card-item-info-rank{
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    top: 0;
    background: rgba(0, 0, 0, 0.1);
    z-index: 1;
    transition: all 0.3s;
}
.card-item-info-rank:hover{
    background: rgba(0, 0, 0, 0);
}
.card-item:hover img{
    transform: scale(1.2);
}
.card-item-info-rank .info{
    position: absolute;
    height: 50px;
    left: 0;
    bottom: 0;
    right: 0;
    background: #666;
    text-align: center;
    padding: 10px;
    cursor: pointer;
    overflow : hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    transition: all 0.3s;
}
.card-item-info-rank .info:hover{
    color: #aaa;
}

.card-item-info:hover{
    background: rgba(0, 0, 0, 0.1);
}
.card-item-info .info{
    position: absolute;
    width: 70%;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
    text-align: center;
}
.read-btn{
    display: inline-block;
    width: 80px;
    margin: 10px auto;
    background: transparent;
    font-size: 12px;
    border: 1px solid #FFF;
    border-radius: 40px;
    padding: 4px 0;
    color: #FFF;
    cursor: pointer;
    transition: all 0.3s;
    
}
.read-btn:hover{
    background: #fff;
    color: #333;
}
.card-item img{
    height: 200px;
    margin-bottom: 0;
    transition: all 0.4s;
}

.card-item-small{
    display: flex;
    background: #222;
    box-sizing: border-box;
}
.card-item-small-info-title{
    overflow : hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    cursor: pointer;
    transition: all 0.3s;
}
.card-item-small-info-title:hover{
    color: #ccc;
}
/* .card-title:hover{
    color: #333;
} */

.img-wrapper{
    width: 120px;
    height: 85px;
    overflow: hidden;
}
.card-item-small img{
    width: 120px;
    height: 85px;
}
.card-item-small-info{
    flex: 1;
    box-sizing: border-box;
    padding: 10px 20px;
    background-color: #666;
}

.card-item-small-info >.time{
    font-size: 13px;
    color: #fff;
    margin-top: 10px;
}

.card-title{
    color: #fff;
    transition: all 0.3s;
}
.main-title{
    font-size: 18px;
}


.card-title a{
    color: #fff;
}

.line{
    width: 50px;
    height: 2px;
    background-color: #fff;
}

.recommend-title{
    overflow : hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
}
